.contents {
    display: grid;
    place-items: center;
    transition: filter 0.2s ease-out, background-color 0.2s ease-out;
    font-size: 16px;
}

:root {
    --map-tiles-filter: brightness(0.6) invert(1) grayscale(70%) contrast(3) saturate(0.3) brightness(0.7);
    color-scheme: dark;
}

.map-tiles {
    filter: var(--map-tiles-filter, none);
}
img.leaflet-marker-icon
{
    background-image: url("data:image/png;base64,");
}
.leaflet-control-container .leaflet-routing-container-hide {
    display: none;
}
.leaflet-interactive {
    stroke: #177ddc;
}
.leaflet-bottom .leaflet-control-attribution svg {
    display: none !important;
}
.marker-span {
    font-weight: bold;
    font-size: 12px;
}